@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    scroll-behavior: smooth;
}
p {
  font-weight: 300;
  color: #111;
}

.nav{position:fixed;top:0;left:0;width:100%;padding:40px 100px;z-index:10000;display:flex;justify-content:space-between;align-items:center;transition:0.5s}.nav.sticky{background:#fff;padding:10px 100px;box-shadow:0 5px 20px rgba(0,0,0,0.05)}.nav .logo{color:#222;font-weight:700;font-size:2em}.nav .logo span{color:#b20504}.nav .navigation{position:relative;display:flex}.nav .navigation li{list-style:none;margin-left:30px}.nav .navigation li a{text-decoration:none;color:#222;font-weight:300}.nav .navigation li a:hover{color:#ff0157}@media (max-width: 779px){.nav{justify-content:center}.nav .logo{display:none}.nav .navigation li{margin:0 10px}}@media (max-width: 340px){.nav .navigation li{font-size:0.75em}}

.banner{position:relative;width:100%;min-height:100vh;background:linear-gradient(#f0ffb2, rgba(255,212,1,0.74902));background-attachment:fixed;overflow:hidden}.banner .leaves{position:absolute;bottom:0;left:0;width:100%;z-index:1000}.banner .set{position:absolute;width:100%;height:100%;top:0;left:0;pointer-events:none}.banner .set div{position:absolute;-webkit-filter:blur(2px);filter:blur(2px)}.banner .set div img{transform:scale(0.5)}.banner .set div:nth-child(1){left:20%;animation:animate 15s linear infinite;animation-delay:-7s}.banner .set div:nth-child(2){left:50%;animation:animate 20s linear infinite;animation-delay:-5s}.banner .set div:nth-child(3){left:70%;animation:animate 20s linear infinite;animation-delay:0s}.banner .set div:nth-child(4){left:0%;animation:animate 15s linear infinite;animation-delay:-5s}.banner .set div:nth-child(5){left:85%;animation:animate 18s linear infinite;animation-delay:-10s}.banner .set div:nth-child(6){left:72%;animation:animate 12s linear infinite}.banner .set div:nth-child(7){left:15%;animation:animate 14s linear infinite}.banner .set div:nth-child(8){left:60%;animation:animate 15s linear infinite}.banner h2{position:relative;color:#d50209;overflow:hidden;font-weight:bold;text-align:center;margin-top:40vh;font-size:3em;opacity:1;visibility:visible;animation:animateText 4s linear forwards}.banner h2 span:nth-child(2){margin-left:30px}@keyframes animate{0%{opacity:0;top:-10%;transform:translateX(20px) rotate(0deg)}10%{opacity:1}20%{transform:translateX(-20px) rotate(45deg)}40%{transform:translateX(-20px) rotate(90deg)}60%{transform:translateX(20px) rotate(180deg)}80%{transform:translateX(-20px) rotate(180deg)}100%{top:90%;transform:translateX(20px) rotate(225deg)}}@keyframes animateText{0%{opacity:0;visibility:hidden}80%{font-size:4em}100%{font-size:3em}}@media (max-width: 779px){.banner h2 span:nth-child(2){display:none}}

.about{position:relative;width:100%;height:100vh;background:#f9ecd8;padding:100px}.about .container{position:relative;width:100%;height:100%;border:3px solid rgba(0,0,0,0.2);border-radius:20px;display:flex}.about .container .content{position:relative;width:50%;height:100%;display:flex;justify-content:center;align-items:center}.about .container .content .intro{display:flex;justify-content:center;align-items:center;flex-direction:column}.about .container .content .intro .img{width:150px;height:150px}.about .container .content .intro .img img{width:100%;height:100%}.about .container .content .intro .text{margin-top:20px;font-family:'Roboto', sans-serif;font-size:1em;font-weight:500;width:70%;letter-spacing:1px}.about .container .content .box{width:100%;height:100%;padding:10px}.about .container .content .box h4{margin-top:20px;text-align:center}.about .container .content .box p{margin-top:20px;font-size:1em;font-weight:500}@media (max-width: 1050px){.about .container .content{width:100%}.about .container .content .intro .text{font-weight:600}.about .container .content:nth-child(2){display:none}}@media (max-width: 553px){.about{padding:100px 50px}}@media (max-width: 378px){.about{padding:100px 20px}}@media (max-width: 300px){.about .container .content .intro .text{font-size:0.8em}.about .container .content .intro .img{width:100px;height:100px}}

.skills{position:relative;width:100%;min-height:100vh;background:#2e2e2e;overflow:hidden;padding:100px}.skills .container{display:flex;justify-content:space-around;margin-top:50px;transition:0.5s}.skills .container:hover .content{opacity:0.05}.skills .container .content{position:relative;width:500px;text-align:center;transition:0.5s}.skills .container .content:hover{opacity:1;transform:scale(1.1)}.skills .container .content:hover .skill{background:linear-gradient(#616161 0%, #333 10%, #222);border:2px solid #000}.skills .container .content:hover .progress{animation:animatePercent 2s ease-in-out forwards}.skills .container .content h2{color:#fff}.skills .container .content .skill{position:relative;display:flex;margin:20px 0;padding:24px 10px 18px;border-radius:8px;overflow:hidden;transition:0.5s}.skills .container .content .skill .Name{position:relative;width:110px;text-align:right;color:#fff;margin-top:-2px;text-transform:uppercase}.skills .container .content .skill .Value{position:relative;width:40px;text-align:left;color:#fff;margin-top:-2px;text-transform:uppercase}.skills .container .content .skill .percent{position:relative;width:calc(100% - 150px);height:20px;margin:0 10px;border-radius:10px;background:#151515;box-shadow:inset 0 0 10px #000;overflow:hidden}.skills .container .content .skill .percent .progress{position:absolute;top:0;left:0;width:70%;height:100%;border-radius:10px;background:#fff;box-shadow:inset 0 0 2px #000}.skills .container .content .skill:nth-child(2) .percent .progress{background:linear-gradient(45deg, #1fe6ff, #673AB7)}.skills .container .content .skill:nth-child(3) .percent .progress{background:linear-gradient(45deg, #3bc0ff, #3f0)}.skills .container .content .skill:nth-child(4) .percent .progress{background:linear-gradient(45deg, #ffee54, #ff00ca)}.skills .container .content .skill:nth-child(5) .percent .progress{background:linear-gradient(45deg, #22ffde, #2196F3)}@keyframes animatePercent{from{width:0}}@media (max-width: 1100px){.skills .container{justify-content:center;align-items:center;flex-direction:column}}@media (max-width: 550px){.skills .container .content{width:400px}}@media (max-width: 470px){.skills .container .content{width:300px}}@media (max-width: 350px){.skills .container .content{width:250px}}

.project{position:relative;width:100%;min-height:100vh;overflow:hidden}.project .container{position:relative;height:100vh;padding:100px;display:flex}.project .container:nth-child(1){background:#b7d8cf}.project .container:nth-child(2){background:#fcf6f0}.project .container::before{position:absolute;content:'';left:0;top:0;width:50%;height:100vh;background:rgba(0,0,0,0.1)}.project .container .content{position:relative;width:50%;height:100%;margin:20px}.project .container .content .title{position:relative;margin-left:35%}.project .container .content .title h1{font-size:3em}.project .container .content .title h4{font-size:1em}.project .container .content .title .btnBx{width:100%;height:50px;display:flex;justify-content:center;align-items:center}.project .container .content .title .btnBx .btn{display:inline-block;padding:10px 15px;cursor:pointer;border:2px solid #222;border-radius:10px;background:transparent;color:#222;font-size:1.2em;font-weight:900;transition:0.5s;margin-top:20px}.project .container .content .title .btnBx .btn:hover{background:#fff}.project .container .content .img{position:absolute;bottom:0;right:0}.project .container .content .summary{margin-left:20%}.project .container .content .summary h2{font-family:'Roboto', sans-serif;font-size:1.5em;font-weight:700;margin-top:50px}.project .container .content .summary ul{margin-top:10px}.project .container .content .summary ul li{font-family:'Roboto', sans-serif;list-style:none;font-size:1.2em;font-weight:500;line-height:40px}.project .container .content .summary p{margin-top:10px;font-size:1em;font-weight:500;line-height:30px}@media (max-width: 1350px){.project .container{justify-content:center;align-items:center}.project .container::before{width:100%}.project .container .content{width:600px}.project .container .content .title{margin:0;text-align:center}.project .container .content .img{width:100%;display:flex;justify-content:center}.project .container .content:nth-child(2){display:none}}@media (max-width: 590px){.project .container .content .title h1{font-size:2em}.project .container .content .img{display:none}}

.contact{position:relative;width:100%;min-height:100vh;background:url(/my-portfolio/static/media/bg6.dd4b3e32.jpg);background-size:cover;overflow:hidden;padding:100px}.contact .container{padding:80px;display:flex}.contact .content:nth-of-type(1){width:50%;text-align:right;font-size:2.5em;font-weight:700}.contact .content:nth-of-type(1) h1{font-family:consolas}.contact .content:nth-of-type(2){width:50%;display:flex;justify-content:center;flex-direction:column;padding-top:30px}.contact .content .data{margin-left:25%}.contact .content .data h2:hover{color:#9c27b0;cursor:pointer}.email::before{content:'\f0e0';font-family:FontAwesome;position:absolute;font-size:1.5em;line-height:1.5em;transform:translateX(-33px)}.git::before{content:'\f09b';font-family:FontAwesome;position:absolute;font-size:1.5em;line-height:1.5em;transform:translateX(-33px)}.til::before{content:'\f02d';font-family:FontAwesome;position:absolute;font-size:1.5em;line-height:1.5em;transform:translateX(-33px)}@media (max-width: 991px){.contact .content:first-child{display:none}.contact .content:nth-child(2){width:100%;align-items:center}}@media (max-width: 430px){.contact .container{padding:0}.contact .content:nth-child(2) .data{margin:0}.contact .content:nth-child(2) .data::before{display:none}}@media (max-width: 340px){.contact .content:nth-child(2) .data h2{font-size:1.2em}}

